// variables

$primary_color: #1EACC7;
$bg_dark: #000;
$text_dark: #222222;
$font_small: 17px;
$font_big: 38px;

// mixins

@mixin cap_dark {
	width: 115px;
	height: 25px;
	background-color: $bg_dark;
	color: #fff;
	margin: 0 auto;
	font-weight: 400;
	font-size: 11px;
	line-height: 11px;
	text-transform: uppercase;
	text-align: center;
	padding-top: 7px; }

@mixin cap_light {
	width: 115px;
	height: 25px;
	background-color: #fff;
	color: #070716;
	margin: 0 auto;
	font-weight: 400;
	font-size: 11px;
	line-height: 11px;
	text-transform: uppercase;
	text-align: center;
	padding-top: 7px; }

@mixin section_header {
	color: $text_dark;
	text-transform: uppercase;
	text-align: center;
	margin: 0;
	margin-top: 33px;
	font-weight: 700;
	font-size: $font_big;
	line-height: 48px; }

@mixin section_subheader {
	font-weight: 400;
	font-size: $font_small;
	line-height: 21px;
	color: $text_dark;
	text-align: center; }


// document styles

* {
	font-family: 'Roboto', sans-serif; }

// header

header {
		min-height: 52px;
		position: relative; }


nav {
	height: 52px;
	background-color: #212121;
	position: fixed;
	width: 100%;
	z-index: 3; }

.menu {
	padding: 14px 70px;
	margin: 0;
	list-style-type: none;
	display: flex;
	justify-content: space-between;

	.menu_item {
		border-right: 2px dashed $bg_dark;
		padding-right: 30px;

		&:last-child {
			border: none; } }

	.menu_link {
		font-weight: 300;
		font-size: 15px;
		color: #FFFFFF;
		text-decoration: none; } }

// subheader

.subheader {
	height: 85px;
	background-color: rgba(0, 0, 0, 0.6);

	/*  1-й способ поднять .subheader */

	position: absolute;
	width: 100%;
	top: 52px;

	/* 2-й способ поднять .subheader */

	/* position: relative; */
	/* z-index: 2; */

	.subheader_logo {
		display: block;
		min-width: 100%;
		margin-top: 16px; }

	.subheader_txt {
		margin-top: 6px;
		font-weight: 100;
		font-size: 13px;
		line-height: 13px;
		color: #FFFFFF;
		opacity: 0.5; }

	.call {
		font-weight: 300;
		text-align: end;
		color: #FFFFFF;
		font-size: 12px;
		line-height: 12px;
		margin-top: 20px; }

	.subheader_number {
		display: block;
		margin-top: 10px;
		font-size: $font_small + 5;
		line-height: 12px;
		font-weight: 400;
		text-decoration: none;
		color: #FFFFFF; }

	.call_btn {
		width: 172px;
		height: 46px;
		margin-top: 20px;
		font-weight: 400;
		font-size: 14px; } }

.btn {
	border: none;
	border-radius: 4px;
	background: linear-gradient(0deg, $primary_color 0%, #4CE2FF 100%);
	box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
	text-align: center;
	text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
	color: #fff;
	text-transform: uppercase; }

/* promo */

.promo {
	padding: 150px 0 135px 0;
	text-align: center;
	color: #FFFFFF;
	min-height: 800px;
	background: url(../img/bg/uber_bg.jpg) center (center / cover) no-repeat grey;

	/* 2-й способ понять .subheader */

	/* transform: translateY(-85px) */
	/* z-index: 1 */

	h1 {
		font-weight: 900;
		font-size: 60px;
		line-height: 42px;
		margin: 0;
		/* color: #FFFFFF; */
		/* text-align: center; */ }

	.promo_subheader {
		margin-top: 23px;
		margin-bottom: 0;
		font-weight: 700;
		font-size: $font_big - 2;
		line-height: 24px;
		/* color: #FFFFFF; */
		/* text-align: center; */ }

	.promo_txt {
		font-weight: 400;
		font-size: $font_small + 1;
		line-height: 24px;
		margin-top: 22px;
		/* color: #FFFFFF; */
		/* text-align: center; */ }

	.promo_btn {
		margin-top: 115px;
		width: 259px;
		height: 63px;
		font-weight: 400;
		font-size: 18px; } }

/* benefits */

.benefits {
	padding: 19px 0 64px 0;

	.cap {
		@include cap_dark; }

	.section_header {
		@include section_header; } }


.benefits {
	&_wrapper {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 25px;
		min-height: 150px;
		.benefits_icon {
			width: 116px;
			height: 116px;
			background-color: $primary_color;
			border-radius: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
			margin-right: 25px; }
		.benefits_info {
			max-width: 343px;
			.benefits_subheader {
				color: $primary_color;
				font-weight: 400;
				font-size: $font_small;
				line-height: 21px; }
			.benefits_descr {
				font-weight: 300;
				font-size: $font_small - 2;
				line-height: 21px;
				color: #202020; } } } }

/* mobile */

.mobile {
	min-height: 864px;
	padding: 46px 0 80px 0;
	background: url('../img/bg/mobile_bg.jpg') center (center / cover) no-repeat grey;

	.cap {
		@include cap_dark; }

	.section_header {
		@include section_header; }

	.section_subheader {
		@include section_subheader; }

	.card {
		width: 320px;
		height: 280px;
		background: url('#') center (center / cover) no-repeat grey;
		padding: 82px 0 34px 0;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		margin-top: 20px;

		&.bg_1 {
			background-image: url('../img/mobile/ordering.jpg'); }

		&.bg_2 {
			background-image: url('../img/mobile/delivery.jpg'); }

		&.bg_3 {
			background-image: url('../img/mobile/prices.jpg'); }

		&.bg_4 {
			background-image: url('../img/mobile/cashless.jpg'); }

		&.bg_5 {
			background-image: url('../img/mobile/feedback.jpg'); }

		&.bg_6 {
			background-image: url('../img/mobile/share.jpg'); }

		.card_descr {
			text-align: center;
			font-weight: 400;
			font-size: 24px;
			line-height: 31px;
			text-transform: uppercase;
			color: #FFFFFF; }

		.plus {
			cursor: pointer;
			width: 29px;
			height: 29px;
			border-radius: 100%;
			background-color: $bg_dark;
			margin: 0 auto;
			display: flex;
			justify-content: center;
			align-items: center; } } }

/* choice styles */

.dark_bg {
	padding-top: 56px;
	background-color: #070716;
	height: 502px;

	.container {
		position: relative; }

	img {
		/* position: absolute; */
		margin: 0 auto;
		margin-top: 54px;

		/* width: 760px */
		/* height: 480px */ }

	.cap {
		@include cap_light; }

	.section_header {
		@include section_header;
		color: #fff; }

	.section_subheader {
		@include section_subheader;
		color: #fff; } }

.light_bg {
	z-index: 1;
	padding: 210px 0 138px 0;
	background: url('../img/bg/mobile_bg.jpg') center (center / cover) no-repeat grey;
	min-height: 400px;

	.bg_caption {
		font-weight: 300;
		font-size: $font_small;
		line-height: 24px;
		text-align: center;
		margin: 0 auto;
		color: $text_dark; } }
